<template>
  <div class="exam-batch">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <el-form :model="form" :rules="rules" ref="form" label-width="80px" class="form">
        <el-form-item label="批次名称" prop="title">
          <el-input v-model="form.title" class="input" placeholder="请输入批次名称"></el-input>
        </el-form-item>
        <el-form-item label="招生类别" prop="secondRecruitTypeId">
          <el-radio-group v-model="form.secondRecruitTypeId">
            <el-radio @change="parentId = item.parentId"  v-for="(item,key) in typeList" :key="key" v-if="key < 4" :label="item.id" border>{{item.name}}</el-radio>
            <br>
            <el-radio @change="parentId = item.parentId" class="mt15" v-for="(item,key) in typeList" :key="key" v-if="key >= 4" :label="item.id" border>{{item.name}}</el-radio>
          </el-radio-group>
        </el-form-item>
        <!--<el-form-item v-if="!isCulture" label="特长类别" prop="specialtyType">-->
          <!--<el-cascader-->
              <!--v-model="specialtyType"-->
              <!--class="input"-->
              <!--@active-item-change="handleItemChange"-->
              <!--:options="specialtyList"></el-cascader>-->
        <!--</el-form-item>-->

        <div class="separated-text">笔试设置</div>
        <el-form-item label="考试时间" prop="examTime">
          <el-date-picker
              v-model="form.examTime"
              type="date"
              placeholder="选择考试时间">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="考点设置" prop="examCentreList">
          <resize-table>
          <table class="table">
            <thead>
            <tr>
              <th width="45%">考点名称</th>
              <th width="35%">最多容纳考生数(人)</th>
              <th width="20%">操作</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(item,key) in form.examCentreList">
              <td>{{getName(item.trainInstitutionId)}}</td>
              <td>{{item.capacity}}</td>
              <td>
                <span @click="removeTest(key)" class="oper">删除</span>
              </td>
            </tr>
            <tr v-show="testAddStatus">
              <td>
                <el-select v-model="testAdd" default-first-option filterable placeholder="请选择" size="small">
                  <el-option
                      :disabled="form.examCentreList.some(e=> item.id === e.trainInstitutionId)"
                      v-for="(item,key) in testList" :key="key" :value="item.id" :label="item.name"></el-option>
                </el-select>
              </td>
              <td>
                <el-input-number v-model="testNum" controls-position="right" size="small" :min="1" :max="10000"></el-input-number>
              </td>
              <td>
                <span @click="affirmAddTest" class="oper mr10">完成</span>
                <span @click="testAddStatus = false" class="oper">删除</span>
              </td>
            </tr>
            <i @click="addTestFun()" class="icon el-icon-circle-plus"></i></tbody>
          </table>
          </resize-table>
        </el-form-item>
        <div class="separated-text">面试设置</div>
        <el-form-item label="面试时间">
          <el-date-picker
              v-model="form.interviewTime"
              type="date"
              placeholder="选择考试时间">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="面试地点">
          <el-radio-group v-model="form.campusId">
            <el-radio :key="key" v-for="(item,key) in campus" :label="item.id" border>{{item.name}}</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div class="btns">
        <div @click="doSubmit" class="btn-submit mr15">确认</div>
        <div @click="$router.go(-1)" class="btn-clear">取消</div>
      </div>
    </main-part>
  </div>
</template>
<script>
  export default {
    data() {
      var vm = this
      var checkExamTime = (rule, value, callback) => {
        if(!this.isCulture){
          callback();
        }
        if (!value) {
          callback(new Error('请选择考试时间'));
        } else {
          callback();
        }
      };
      var checkList = (rule, value, callback) => {

        if(!this.isCulture && !this.editStatus){
          callback();
        }
        if (!this.form.examCentreList.length) {
          callback(new Error('至少需要选择一个考点'));
        } else {
          callback();
        }
      };
      var checkSpecialtyType = (rule, value, callback) => {
        if (!this.specialtyType.length) {
          callback(new Error('请选择特长类别'));
        } else {
          callback();
        }
      };
      var checkId = (rule, value, callback) => {
        if (!this.form.firstRecruitTypeId && !this.form.secondRecruitTypeId) {
          callback(new Error('请选择招生类别'));
        } else {
          callback();
        }
      };
      return {
        // 是否为编辑状态
        editStatus: this.$route.params.id !== '0',
        // 当前是文化生还是特长生
        isCulture: this.$route.query.recruitType === 'CULTURE',
        nav: [{name: "考试管理", path: "go(-1)"},
          {name: this.$route.params.id !== '0' ? "编辑招生批次": "新增招生批次", path: ""}],
        form: {
          recruitType: this.$route.query.recruitType,
          secondRecruitTypeId: '',
          title: '',
          examTime: '',
          examCentreList: [],
          interviewTime: ''
        },
        rules: {
          title: { required: true, message: '请输入批次名称', trigger: 'blur' },
          secondRecruitTypeId: { required: true, validator: checkId, trigger: 'change' },
          examTime: { required: this.$route.query.recruitType === 'CULTURE', validator: checkExamTime, trigger: 'change' },
          specialtyType: { required: true, validator: checkSpecialtyType, trigger: 'change' },
          examCentreList: { required: this.$route.query.recruitType === 'CULTURE' || this.$route.params.id !== '0', validator: checkList, trigger: 'blur' },
        },
        // 所有考点
        testList: [],
        // 新增时选择的考点名称
        testAdd: '',
        // 新增时最多容纳考生数
        testNum: 1,
        // 是否为新增考点状态
        testAddStatus: false,
        // 招生类别
        typeList: [],
        // 记录一级招生类别 接口需要
        parentId: null,
        // 特长专业列表
        specialtyList:[],
        // 选中的特长类别
        specialtyType: [],
        // 面试地点 校区
        campus: []
      }
    },
    methods: {
      // 查询所有考点
      async getList(){
        this.testList = await this.$ajax({
          url: '/rinstitution/queryTrainInstitutionList.do'
        })
      },
      // 新增考点
      addTestFun(){
        if(!this.testAddStatus){
          this.testAdd = ''
          this.testNum = 1
          this.testAddStatus = true
        }
      },
      // 删除考点
      removeTest(key){
        this.form.examCentreList.splice(key,1)
        if(this.form.examCentreList.length === 0){
          this.$refs.form.validateField('examCentreList')
        }
      },
      // 确认新增考点
      affirmAddTest(){
        this.form.examCentreList.push({
          trainInstitutionId: this.testAdd,
          capacity: this.testNum
        })
        this.$refs.form.clearValidate(['examCentreList'])
        this.testAddStatus = false
      },
      // 根据考点ID查询考点名称
      getName(id){
        let name = ''
        this.testList.some(e=>{
          if(e.id === id){
            name = e.name
            return true
          }
        })
        return name
      },
      // 获取招生类别
      async getType(){
        this.typeList = []
        this.type = []
        let res = await this.$ajax({
          url: '/dict/queryDictList.do',
          query:{
            dictType: this.isCulture ? 'RECRUIT_CULTURE': 'RECRUIT_SPECIALITY'
          }
        })
        if(!this.isCulture){
          this.typeList = res
          return
        }
        res.forEach( async  e=>{
          let res = await this.$ajax({
            url: '/dict/queryDictList.do',
            query:{
              parentId: e.id,
              dictType: e.dictType
            }
          })
          if(!this.typeList.length){
            this.typeList = this.typeList.concat(res)
          }else{
            this.typeList = res.concat(this.typeList)
          }
        })

      },
      // 获取特长专业
      async getSpecialty(){
        let res = await this.$ajax({
          url: '/dict/queryDictList.do',
          query:{
            dictType: 'SPECIALITY'
          }
        })
        this.specialtyList = []
        res.forEach(e=>{
          this.specialtyList.push({
            label: e.name,
            value: e.id,
            children: []
          })
        })
      },
      // 动态获取特长专业级联数据
      async handleItemChange(val){
        let res = await this.$ajax({
          url: '/dict/queryDictList.do',
          query:{
            parentId: val[0],
            dictType: 'SPECIALITY'
          }
        })
        let arr = []
        res.forEach(e=>{
          arr.push({
            label: e.name,
            value: e.id,
          })
        })
        this.specialtyList.forEach((e,i)=>{
          if(e.value === val[0]){
            this.specialtyList[i].children = arr
          }
        })
      },
      // 提交
      async doSubmit(){
        await this.$refs.form.validate()
        let obj = this.form
        if(obj.examTime){
          obj.examTime = new Date(obj.examTime).$Format('yyyy-MM-dd hh:mm:ss')
        }
        if(obj.interviewTime){
          obj.interviewTime = new Date(obj.interviewTime).$Format('yyyy-MM-dd hh:mm:ss')
        }
        if(this.isCulture){
          obj.firstRecruitTypeId = this.parentId
        }else{
          obj.firstRecruitTypeId = this.form.secondRecruitTypeId
          obj.secondRecruitTypeId = null
        }

        let url = '/rbatch/addBatch.do'
        let text = '新增成功'
        if(this.editStatus){
          url = '/rbatch/updateBatch.do'
          text = '更新成功'
          this.form.batchId = this.form.id
        }
        await this.$ajax({
          url,
          el: '.btn-submit',
          noText: true,
          query: this.form,
        })
        this.$remind(text)
        this.$router.push(`/recruit/exam?recruitType=${this.$route.query.recruitType}`)
      },
      // 获取所有的校区
      async getCampusList() {
        this.campus = await this.$ajax({
          url: '/campus/queryCampusList.do'
        })
      },
      //
      async getDetail(){
        this.form = await this.$ajax({
          url: '/rbatch/getBatch.do',
          query:{
            id: this.$route.params.id
          }
        })
        // 如果是特长生
        if(!this.isCulture){
          this.form.secondRecruitTypeId = this.form.firstRecruitTypeId
          this.form.firstRecruitTypeId = null
          // await this.handleItemChange([this.form.firstRecruitTypeId])
          // this.specialtyType = [this.form.firstRecruitTypeId,14]
        }else{
          this.parentId = this.form.firstRecruitTypeId
        }

      }
    },
    mounted(){
      if(!this.isCulture){
        this.getSpecialty()
      }
      this.getCampusList()
      this.getType()
      this.getList()
      if(this.editStatus){
        this.getDetail()
      }
    }
  }
</script>
<style lang="sass" scoped>
  .exam-batch
    .form
      margin-top: 20px
      /deep/ .el-radio.is-bordered
        margin-left: 0
        margin-right: 15px
      .input
        width: 420px
      .table
        width: 600px
        position: relative
        .icon
          position: absolute
          top: 10px
          right: -40px
          color: $gray
          font-size: 24px
          transition: all .4s
          cursor: pointer
          &:hover
            transform: rotate(360deg)
    .btns
      margin-left: 80px
</style>
